import React, { useState } from "react";
import "./App.css";
import { BrowserRouter as Router, Route, Switch, Link } from "react-router-dom";
import Home from "./Home.js";
import "semantic-ui-css/semantic.min.css";
import userData from "./employee.json";
import Add from "./images/fondo.jpg";
import {
  Header,
  Menu,
  Icon,
  Modal,
  Button,
  Input,
  Segment,
  Checkbox,
} from "semantic-ui-react";
import Tareas from "./tareas";
import Consultas from "./consulta";
import Registrar from "./registrar";
import Categorias from "./tipos";

function App() {
  let iniciado = null;
  let caption = "Sign In";
  if (sessionStorage.getItem("user")) {
    iniciado = sessionStorage.user;
    caption = "Sign Out";
  } else if (localStorage.getItem("user")) {
    iniciado = localStorage.user;
    caption = "Sign Out";
  }
  const [user, setUser] = useState(iniciado);
  const [session, setSession] = useState(caption);
  const [open, setOpen] = React.useState(false);
  const [password, setPassword] = useState("");
  const [activeItem, setActiveItem] = useState("");
  const [recordar, setRecordar] = useState(false);

  const handleItemClick = (e, { name }) => {
    setActiveItem(name);
  };
  const onChangeHandler = (event) => {
    const { name, value } = event.currentTarget;

    if (name === "userPassword") {
      setPassword(value);
    }
  };

  const login = (event, password) => {
    event.preventDefault();
    var found = false;
    for (var i = 0; i < userData.length; i++) {
      if (userData[i].id === password) {
        found = true;
        setUser(userData[i].Nombre);
        sessionStorage.user = userData[i].Nombre;
        if (recordar) {
          localStorage.user = userData[i].Nombre;
        }
        setSession("Sign Out");
        break;
      }
    }
    if (!found) alert(`Usuario No encontrado`);
  };

  return (
    <Router basename={"/sasso-app"}>
      <div style={{ padding: "2%", backgroundImage: `url(${Add})` }}>
        <Segment style={{ backgroundColor: "gray" }}>
          <Header textAlign="center">
            BIENVENIDO {user ? user.toUpperCase() : null}
          </Header>
        </Segment>
        <Segment
          style={{
            backgroundColor: "whitesmoke",
            height: "75vh",
            overflowY: "scroll",
          }}
        >
          <Switch>
            <Route
              exact
              path="/home"
              render={(props) => <Home {...props} user={user} />}
            />
            <Route
              path="/registros"
              render={(props) => <Registrar {...props} user={user} />}
            />
            <Route
              path="/tareas"
              render={(props) => <Tareas {...props} user={user} />}
            />
            <Route
              path="/observaciones"
              render={(props) => <Consultas {...props} user={user} />}
            />
            <Route
              path="/"
              render={(props) => <Registrar {...props} user={user} />}
            />
          </Switch>
        </Segment>
        <Menu fluid widths={5} icon="labeled" inverted>
          <Menu.Item
            name="estadistica"
            active={activeItem === "estadistica"}
            onClick={handleItemClick}
            as={Link}
            to="/home"
          >
            <Icon name="chart line" />
            Estadisticas
          </Menu.Item>
          <Menu.Item
            name="observaciones"
            active={activeItem === "observaciones"}
            onClick={handleItemClick}
            as={Link}
            to="/observaciones"
          >
            <Icon name="search" />
            Observaciones
          </Menu.Item>
          <Menu.Item
            name="registrar"
            active={activeItem === "registrar"}
            onClick={handleItemClick}
            as={Link}
            to="/registros"
          >
            <Icon name="add square" />
            Registrar
          </Menu.Item>
          <Menu.Item
            name="tareas"
            active={activeItem === "tareas"}
            onClick={handleItemClick}
            as={Link}
            to="/tareas"
          >
            <Icon name="tasks" />
            Tareas
          </Menu.Item>
          <Modal
            basic
            onClose={() => {
              setOpen(false);
            }}
            onOpen={() => setOpen(true)}
            open={open}
            size="large"
            trigger={
              <Menu.Item
                name="usuario"
                active={activeItem === "usuario"}
                onClick={handleItemClick}
              >
                <Icon name="user circle" color={user ? "green" : "red"} />
                {user ? user : "Usuario"}
              </Menu.Item>
            }
          >
            {!user ? (
              <Segment textAlign="center">
                <Header icon>
                  <Icon name="key" />
                  Sign In
                </Header>
                <Header as="h3">
                  <Modal.Content>
                    <Input iconPosition="left" placeholder="Password">
                      <Icon name="key" />
                      <input
                        name="userPassword"
                        type="password"
                        onChange={(event) => onChangeHandler(event)}
                      />
                    </Input>
                    <Modal.Description>
                      <p>
                        <Checkbox
                          label="Recordar Usuario"
                          onChange={(e, { checked }) => {
                            checked ? setRecordar(true) : setRecordar(false);
                          }}
                        />
                      </p>
                    </Modal.Description>
                  </Modal.Content>
                </Header>
                <Modal.Actions>
                  <Button color="red" inverted onClick={() => setOpen(false)}>
                    <Icon name="remove" /> Cancel
                  </Button>
                  <Button
                    color="green"
                    inverted
                    onClick={(event) => {
                      login(event, password);
                      setOpen(false);
                    }}
                  >
                    <Icon name="checkmark" /> Sign In
                  </Button>
                </Modal.Actions>
              </Segment>
            ) : (
              <Segment
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                }}
                textAlign="center"
              >
                <Menu inverted vertical>
                  <Menu.Item
                    name="contact"
                    active={activeItem === "contact"}
                    as="a"
                    href="https://wa.me/50769493169?text=Hola%20Chino"
                  />
                  <Menu.Item
                    name={session}
                    onClick={() => {
                      setUser("");
                      sessionStorage.removeItem("user");
                      localStorage.removeItem("user");
                      iniciado = null;
                      setSession("Sign In");
                      setOpen(false);
                    }}
                  />
                </Menu>
              </Segment>
            )}
          </Modal>
        </Menu>
      </div>
    </Router>
  );
}

export default App;
